



$darkThemeColors: (
        textColor: #f5f5f5,
        headerNameColor: #fff,
        secondTextColor: #aaa,
        thirdTextColor: #d5d5d5,
        projTitlesColor: #e5e5e5,
        footerLinksColor: #ddd,
        backColor: #222,
        footerBackColor: #252525,
        secondColor: #aaa,
        thirdColor: #d5d5d5,
        lineColorMain: #353535,
        lineColor: #007ced,
        photoBorderColor: #444
);
$textColor: map-get($darkThemeColors, textColor);
$headerNameColor: map-get($darkThemeColors, headerNameColor);
$secondTextColor: map-get($darkThemeColors, secondTextColor);
$thirdTextColor: map-get($darkThemeColors, thirdTextColor);
$projTitlesColor: map-get($darkThemeColors, projTitlesColor);
$footerLinksColor: map-get($darkThemeColors, footerLinksColor);
$backColor: map-get($darkThemeColors, backColor);
$footerBackColor: map-get($darkThemeColors, footerBackColor);
$secondColor: map-get($darkThemeColors, secondColor);
$thirdColor: map-get($darkThemeColors, thirdColor);
$lineColorMain: map-get($darkThemeColors, lineColorMain);
$lineColor: map-get($darkThemeColors, lineColor);
$photoBorderColor: map-get($darkThemeColors, photoBorderColor);


