@import './src/common/styles/sass/mixins.scss';
@import './src/common/styles/sass/variables.scss';

.header {

  display: flex;
  justify-content: space-between;
  align-items: center;
  height: 100px;
  padding: 0 0 50px 60px;

}

.logo {
  display: flex;
  justify-content: start;
  align-items: center;
}

.logo img {
  width: 55px;
  height: 55px;
}

.logo h2 {
  color: $headerNameColor;
  @include text(600, 20px)
}

.lastName {
  @include text(400, 20px)
}

@media screen and (max-width: 768px) {
  .header {
    padding: 0 20px 50px 25px;
  }.logo img {
     width: 40px;
     height: 40px;
   }

}