@import "./common/styles/sass/variables";
@import "./common/styles/sass/mixins";


.App {
    padding-top: 40px;
    position: relative;
}

@mixin darkTheme {
    background-color:black;
    color:white;
}
@mixin lightTheme {

}

body.light {
    background-color:$backColor;
    color:  $textColor
}

body.dark {
    background-color:$backColor;
    color:  $textColor
}

.toggleTheme {
    @include text(400,12px);
    position: fixed;
    top: 30%;
    right: 15px;
    transform: translateY(-50%);
    background-color: #fff;
    border: 2px solid #000;
    padding: 10px 20px;
    cursor: pointer;
}

.toggleTheme:hover {
    background-color: $textColor;
    color: #fff;
}

@media screen and (max-width: 768px) {
    .App {
        padding-top: 15px;
    }
    .toggleTheme {
        @include text(400,12px);
        position: absolute;
        top: 10%;
    }
}
@media screen and (max-width: 550px) {
    .toggleTheme {
        @include text(400,12px);
        position: absolute;
        top: 5%;
    }
}

